@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-image: url(/Many0507/weather-app-reactjs/static/media/background.7cced4f2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  font-family: 'Ubuntu', sans-serif;
  min-height: 100vh; }

.container {
  background-size: cover;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 5rem;
  height: 100vh; }
  @media screen and (max-width: 680px) {
    .container {
      padding: 1rem; } }

.header {
  background: #2193b0;
  background: -webkit-linear-gradient(to right, #6dd5ed, #2193b0);
  background: -webkit-gradient(linear, left top, right top, from(#6dd5ed), to(#2193b0));
  background: -webkit-linear-gradient(left, #6dd5ed, #2193b0);
  background: linear-gradient(to right, #6dd5ed, #2193b0);
  color: #fff;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 4rem;
  top: 0; }
  .header__title {
    letter-spacing: .5rem;
    font-weight: normal;
    font-size: 2rem; }
    @media screen and (max-width: 680px) {
      .header__title {
        font-size: 1.2rem; } }

.weather-form {
  position: absolute;
  top: 9rem;
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center; }

.form {
  padding: 2rem;
  background: #fff;
  border-radius: 10px;
  box-shadow: 2px 2px 3px #000; }
  .form__title {
    font-size: 1.5rem;
    margin-bottom: 5px;
    color: #2b2b2b; }
  .form__input {
    padding: .5rem;
    border: 2px solid #ddd;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #eee;
    margin-right: 6px;
    font-size: 1rem; }
    .form__input:focus {
      outline: 2px solid #2193b0; }
    @media screen and (max-width: 680px) {
      .form__input {
        display: block;
        width: 100%;
        margin-bottom: .5rem;
        margin-right: 0; } }
  .form__btn {
    background: #6dd5ed;
    padding: .6rem 2rem;
    border-radius: 10px;
    border: none;
    font-size: 1rem;
    letter-spacing: .1rem;
    -webkit-transition: ease-in .1s;
    transition: ease-in .1s; }
    .form__btn:hover {
      background: #2193b0;
      color: #fff; }
  @media screen and (max-width: 680px) {
    .form {
      width: 100%;
      padding: 1rem;
      width: 90%;
      text-align: center; } }

.weather-app {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around; }
  .weather-app__resume {
    padding: 1rem;
    width: 40%;
    text-align: center;
    background: #fafafa;
    border-radius: 10px;
    box-shadow: 1px 1px 5px #2b2b2b; }
    @media screen and (max-width: 980px) {
      .weather-app__resume {
        width: 95%;
        margin-bottom: 1rem; } }
  .weather-app__data {
    padding: 1rem;
    width: 40%;
    background: #fafafa;
    text-align: center;
    border-radius: 10px;
    box-shadow: 1px 1px 5px #2b2b2b; }
    @media screen and (max-width: 980px) {
      .weather-app__data {
        width: 95%;
        margin-bottom: 1rem; } }
  @media screen and (max-width: 980px) {
    .weather-app {
      margin-top: 12rem;
      flex-direction: column;
      height: auto; } }
  @media screen and (max-width: 680px) {
    .weather-app {
      margin-top: 17rem; } }

.weather-name,
.weather-temp,
.weather-description {
  padding: 1rem 0; }

.weather-name {
  color: #333333;
  font-size: 2rem;
  letter-spacing: .5rem;
  text-align: center;
  font-weight: bold; }

.weather-code {
  color: #888888; }

.weather-icon {
  width: 65px;
  margin-top: .5rem;
  border-radius: 50%;
  background-color: #2193b0;
  box-shadow: 0 0 4px 3px #ddd; }

.weather-temp {
  color: #333333;
  font-size: 1.5rem;
  letter-spacing: .3rem; }
  .weather-temp__metric {
    color: #888888; }

.weather-description {
  font-size: 1.5rem;
  letter-spacing: .3rem;
  color: #888888; }

.weather-maxmintemp,
.weather-humedity,
.weather-wind {
  padding: 1rem 0;
  box-shadow: 0 2px #ddd; }

.weather-maxmintemp {
  font-size: 1.2rem;
  color: #333333;
  letter-spacing: .1rem;
  line-height: 1.7rem; }

.weather-maxtemp {
  color: #888888; }

.weather-mintemp {
  color: #888888; }

.weather-humedity {
  font-size: 1.2rem;
  color: #333333;
  letter-spacing: .1rem; }
  .weather-humedity__value {
    color: #888888; }

.weather-wind {
  font-size: 1.2rem;
  color: #333333;
  letter-spacing: .1rem; }
  .weather-wind__value {
    color: #888888; }

.weather-error {
  text-align: center;
  color: #e41a1a;
  font-weight: bold;
  text-shadow: 0.2px 0.2px #000;
  margin-top: 1rem;
  letter-spacing: .2rem; }

